<template>
  <div>
        <h3 class="text-center">Relatorio 01 - Listar Cestas Básicas Entregues (Por Beneficiado, Data Início e Data Término)</h3>
        <div class="container">
            <table class="table table-borderless table-striped table-image mt-4">
                <thead>
                    <tr>
                        <th class="text-center">ID</th>
                        <th>Beneficiado</th>
                        <th>Quantidade de Cestas</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="entrega in entregas" v-bind:key="entrega.id">
                        <td class="text-center">{{entrega.id}}</td>
                        <td>{{entrega.beneficiado.nome}}</td>
                        <td>{{entrega.qtdCestas}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import DataService from '../service/Relatorio01DataService';
export default {
    name: "Relatório 01",
    data() {
        return {
            entregas: [],
            message: ""
        }
    },
    methods: {
        refresh() {
            DataService.relatorio01()
            .then((response) => {
                this.entregas = response.data;
            });
        }
    },
    created() {
        this.refresh();
    }
}
</script>

